@import 'node_modules/bootstrap/scss/functions';
@import 'node_modules/bootstrap/scss/variables';

// Override bootstrap variables
$zindex-modal-backdrop: 100000;
$zindex-modal: 100005;
$bookly: rgb(244, 102, 47);

$theme-colors: () !default;
$theme-colors: map-merge(
                (
                        "primary": $primary,
                        "secondary": $secondary,
                        "success": $success,
                        "info": $info,
                        "warning": $warning,
                        "danger": $danger,
                        "light": $light,
                        "dark": $dark,
                        "bookly": $bookly
                ),
                $theme-colors
);

@import 'node_modules/bootstrap/scss/bootstrap.scss';
@import 'daterangepicker';
// Override WordPress themes styles

.fade {
    opacity: 1;
}

.modal-backdrop.fade.show {
    opacity: .5;
}


.bookly-search-form {
    .bookly-search-form-calendar {
        max-width: 100%!important;
        & > div {
            max-width: 100%!important;
        }

        .grid {
            max-width: 100%!important;
        }
    }
}
.form-floating {
    select {
        &+label {
            font-size: 12px;
        }
    }
}

input {
    max-width: none!important;
}
input[type="radio"] {
    box-shadow: none;
}

.bookly-search-form-calendar {
    .button.label {
        font-size: 12px;
        font-weight: bold;
        color: black;
    }
    .legend {
        font-size: 12px;
        font-weight: bold;
    }

    .controls {
        .button {
            height: 60px;
            text-decoration: none;
        }
    }
}

.bookly-search-form-phone {
    .intl-tel-input {
        width:100%;
        input {
            padding: 1rem 0.75rem 1rem 52px;
        }
    }
}